import React from 'react'

import ImageProBono from '../../assets/images/team01.jpg'
import ImageComercial from '../../assets/images/team02.jpg'
import ImageParticipate from '../../assets/images/team03.jpg'
import SectionTitle from '../../components/SectionTitle'

import { Container, Box, Image, Content, Text } from './styles'

const Projects = () => (
    <Container id="projects">
    
        <Box>
            <Image src={ImageProBono} alt='projetos pro-bonos' />

            <Content>
                <SectionTitle title="Projetos Pro-Bonos" />
                <Text>
                Projetos Web/Mobile para organizaes sem fins lucrativos.<br></br>
                Excelente oportunidade para os membros que<br></br> esto comeando na rea de tecnologia.<br></br>
                Ajuda as organizaes/iniciativas na Contentulgao de suas<br></br> aes/atividades/campanhas.<br></br>
                Apoio da comunidade para desenvolver <br></br>habilidades profissionais.
                </Text>
            </Content>
        </Box>

        <Box>
            <Content>
                <SectionTitle title="Projetos Comerciais" />
                <Text>
                Projetos Web/Mobile para empresas e organizaes.<br></br>
                Oportunidade para os membros participarem de projetos reais por meio da prestao de servios.<br></br>  
                Qualidade no servio prestado.<br></br>
                Apoio e incentivo ao empreendedorismo feminino.<br></br>
                Apoio da Comunidade, de leads tcnicas com mais experincia de mercado.
                </Text>
            </Content>
            
            <Image src={ImageComercial} alt='projetos comerciais' /> 
        </Box>

        <Box>
            <Image src={ImageParticipate} alt='projetos pro-bonos' />

            <Content>
                <SectionTitle title="Como Posso Participar?" />
                <Text>
                Membros do WWcode Recife participando de<br></br> grupos de estudos e meetups prticos.<br></br>
                Lderes/Voluntrios do WWCode apoiando com<br></br> mentoria e liderando times.<br></br>
                Buscamos pessoas comprometidas, ticas, disponveis,<br></br> que gostem de aprender, de compartilharconhecimento <br></br> e de trabalhar em equipe.
                </Text>
            </Content>
        </Box>

         </Container>  
    );

    export default Projects;



